.searchInputs {
    display: flex;
}

.search input {
    background-color: white;
    border: 0;
    border-radius: 2px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    font-size: 36px;
    padding: 15px;
    height: 30px;
    width: 660px;
}

.searchIcon {
    height: 60px;
    width: 50px;
    background-color: white;
    display: grid;
    place-items: center;
}

input:focus {
    outline:none;
}

.dataResult{
    margin-top: 5px;
    width: 580px;
    height: 200px;
    background-color: white;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
    display: none;
}

.dataResult .dataItem{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
}

.dataItem p{
    margin-left: 50px;
}

a {
    text-decoration:  none;
}

a:hover {
    background-color: lightgrey;
}

#clearBtn{
    cursor: pointer;
} 